body {
    background-image: url(/static/media/bg.16db2177.png);
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    color: white;
}

.title {
    text-align: center;
    padding: 60px 0;
    background-color: rgba(0, 0, 0, .8);
    margin: 0 0 25px 0;
}

.block {
    position: relative;
    box-sizing: border-box;
    margin-bottom: 90px;
    overflow: hidden;
    text-align: center;
    color: #fff;
    border-radius: 5px;
    -webkit-transition: rotate 0.5s ease-in-out;
    transition: rotate 0.5s ease-in-out;
    padding: 10px;
}

.block:nth-child(odd):hover {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
}

.block:nth-child(even):hover {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
}

.blocks-wrapper {
    padding: 50px;
}

.block__date {
    font-family: 'Handlee', cursive;
    display: inline-block;
    padding: 15px 27px;
    font-size: 2rem;
    font-weight: bold;
    background: rgba(0, 0, 0, .8);
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    text-shadow: 1px 2px 11px #000;
}

.block__winner {
    position: relative;
    top: -2px;
    font-family: 'Handlee', cursive;
    display: inline-block;
    padding: 15px 27px;
    font-size: 1.5rem;
    font-weight: bold;
    background: rgba(0, 0, 0, .8);
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    text-shadow: 1px 2px 11px #000;
}

.block__photo {
    position: relative;
    border-radius: 5px;
    min-height: 350px;
    max-width: 550px;
    margin: 0 auto;
}

.block__photo .photo {
    border-radius: 5px;
    box-shadow: 0 0px 12px 5px rgba(0, 0, 0, .5);
}

.btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    -webkit-transition: background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.btn:hover {
    cursor: pointer;
}

.btn-info {
    display: block;
    margin: 0 auto;
    color: #fff;
    background-color: #17a2b8;
    border-color: #17a2b8;
}

.btn-info:hover {
    background-color: #138496;
    border-color: #117a8b;
}

.list {
    text-align: left;
}

.list li {
    margin-bottom: 10px;
}

.decoration {
    position: absolute;
    width: 90px;
    left: -20px;
}

.hat {
    top: -26px;
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
}

.balls {
    top: -27px;
    -webkit-transform: rotate(-7deg);
            transform: rotate(-7deg);
}

.bell {
    top: -20px;
}

.block:nth-child(even) .hat {
    top: -31px;
}

.block:nth-child(even) .hat,
.block:nth-child(even) .bell,
.block:nth-child(even) .balls {
    right: -20px;
    left: auto;
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
}

.tree {
    position: absolute;
    bottom: 1px;
    right: -25px;
    height: 120px;
}

@media only screen and (max-width: 1050px) {
    .blocks-wrapper {
        padding: 0;
    }
    .block__photo {
        max-width: 100%;
        min-height: 100%;
    }
    .title img {
        width: 90%;
    }
    .tree {
        display: none;
    }
    .block__photo .photo {
        width: 100%;
    }
    .ReactModal__Content {
        width: auto !important;
        height: auto !important;
    }
}
